body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*  Color Scheme  */
/*  Background: #010a1a #031b37 #033346
    Text: #ffffff
    Accent Text: #a3aab4
    Green: #1efc0f
    Blue: rgba(2, 232, 232, 0.5)
*/
/* Project thumbnails are 3:2 ratio */

@media (max-width: 700px) {
  .App {
    width: 100% !important;
    margin-left: 0 !important;
  }
}

.App {
  text-align: center;
  max-width: 1400px;
  width: 90%;
  min-height: 100vh;
  /* background-color: #010a1a; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #010a1a;
  background: linear-gradient(315deg, #010a1a 0%, #031b37 90%, #033346 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-header {
  font-size: 20px;
  font-weight: bolder;
}

.banner-align {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width:600px){
  img {
    width: 100%;
  }

  #me {
    height: 200px !important;
    width: 200px !important;
  }

  #video {
    width: 100% !important;
  }

  #logo {
    height: 40px !important;
  }
}

img {
	max-height: 300px;
}

#me {
  object-fit: contain; 
  height: 200px; 
  width: 200px; 
  /* border-radius: 50%; */
}

#video {
  aspect-ratio: 16/9;
  width: 520px;
}

#logo {
  height: 70px;
  width: auto;
}

#cert {
  height: 140px;
  width: auto;
}
.wrapper {
  height: 15vh;
  /*This part is important for centering*/
  display: grid;
  place-items: center;
  margin-bottom: 30px;
}

.typing-name {
  width: 23ch;
  animation: typing 2s steps(23), blink .5s step-end 20 alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid;
  font-family: monospace;
  font-size: calc(25px + 1vw) !important;
	font-weight: 500;
	color: #1efc0f;
}

.typing-home {
  width: 26ch;
  animation: typing 2s steps(26), blink .5s step-end 20 alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid;
  font-family: monospace;
  font-size: calc(25px + 1vw) !important;
	font-weight: 500;
	color: #1efc0f;
}

.typing-portfolio {
  width: 14ch;
  animation: typing 2s steps(14), blink .5s step-end 20 alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid;
  font-family: monospace;
  font-size: calc(25px + 1vw) !important;
	font-weight: 500;
	color: #1efc0f;
}

.typing-resume {
  width: 12ch;
  animation: typing 2s steps(12), blink .5s step-end 20 alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid;
  font-family: monospace;
  font-size: calc(25px + 1vw) !important;
	font-weight: 500;
	color: #1efc0f;
}

.typing-contact {
  width: 17ch;
  animation: typing 2s steps(17), blink .5s step-end 20 alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid;
  font-family: monospace;
  font-size: calc(25px + 1vw) !important;
	font-weight: 500;
	color: #1efc0f;
}

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}
.link-text {
	color: #bcbcbc;
	
}
.link-text:hover {
	color: #ffffff;
}

.spacer {
	height: 100px;
}

#thumbnail {
	object-fit: contain;
	max-width: 180px;
	width: 110%;
}

.center {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.rel {
	position: relative;
}

.subtitle {
	color: #bcbcbc;
	font-size: 20px;
}
